'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { User, Role } from '@/types';

import UserFilters from '@/components/admin/users/UserFilters';
import UserTable from '@/components/admin/users/UserTable';
import UserFormDialog from '@/components/admin/users/UserFormDialog';

export default function UsersPage() {
    const [users, setUsers] = useState<User[]>([]);
    const [roles, setRoles] = useState<Role[]>([]);
    const [loading, setLoading] = useState(true);
    
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [editData, setEditData] = useState<User | null>(null);

    const [searchQuery, setSearchQuery] = useState('');
    const [filterRole, setFilterRole] = useState<string>('all');

    const fetchData = async () => {
        setLoading(true);
        try {
            const [resUsers, resRoles] = await Promise.all([
                axios.get('/api/users'),
                axios.get('/api/roles')
            ]);
            setUsers(resUsers.data);
            setRoles(resRoles.data);
        } catch (error) {
            toast.error("Gagal memuat data user.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchData(); }, []);

    const filteredUsers = users.filter((item) => {
        const matchSearch = item.name.toLowerCase().includes(searchQuery.toLowerCase()) || 
                            item.email.toLowerCase().includes(searchQuery.toLowerCase());
        const matchRole = filterRole === 'all' || item.role_id.toString() === filterRole;
        return matchSearch && matchRole;
    });

    const handleCreate = () => {
        setEditData(null);
        setIsDialogOpen(true);
    };

    const handleEdit = (item: User) => {
        setEditData(item);
        setIsDialogOpen(true);
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Yakin hapus user ini?")) return;
        try {
            await axios.delete(`/api/users/${id}`);
            toast.success("User berhasil dihapus.");
            fetchData(); 
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal menghapus.");
        }
    };

    const handleSubmit = async (formData: any, isEdit: boolean) => {
        try {
            if (isEdit && editData) {
                const payload: any = { ...formData };
                if (!payload.password) delete payload.password;
                if (!payload.password_confirmation) delete payload.password_confirmation;

                await axios.put(`/api/users/${editData.id}`, payload);
                toast.success("Data user diperbarui!");
            } else {
                // CREATE
                await axios.post('/api/users', formData);
                toast.success("User baru ditambahkan!");
            }
            setIsDialogOpen(false);
            fetchData(); 
        } catch (error: any) {
            const msg = error.response?.data?.message || "Gagal menyimpan data.";
            toast.error(msg);
        }
    };

    return (
        <div className="space-y-6">
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Manajemen User</h1>
                    <p className="text-gray-500 text-sm">Kelola akun Admin, Karyawan, dan Pelanggan.</p>
                </div>
                <Button className="bg-[#D93F21] hover:bg-[#b9351b]" onClick={handleCreate}>
                    <Plus className="w-4 h-4 mr-2" /> Tambah User
                </Button>
            </div>

            <UserFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
                filterRole={filterRole}
                setFilterRole={setFilterRole}
                roles={roles}
            />

            <UserTable 
                loading={loading}
                users={filteredUsers}
                onEdit={handleEdit}
                onDelete={handleDelete}
            />

            <UserFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                initialData={editData}
                roles={roles}
            />
        </div>
    );
}